.stock-developer {
    &__body{
        @media (max-width:$desktop){
            padding: calc(var(--header-height) + 24px) 0 16px;
            background-color: var(--stand-bg);
            border-radius: 0 0 12px 12px; 
        }
    }
    &__title{
        @media (max-width:$desktop){
            padding: 0 16px; 
        }
    }
    &__filter {
        @media (max-width:$desktop){
            padding: 0 16px;
            margin-top: 16px;
            margin-bottom: 16px 
        }
    }
    &__input{
       @media (max-width:$desktop){
        width: 100%; 
       }
    }
    &__controls {
        .filter__clear {
            padding: 0 37px;
        }

        .filter__go {
            padding: 0 58px;
        }
    }

    &__content {
        margin-top: 32px;
    }
    .tabs-primary{
        &__container{
            background-color: transparent;
        }
    }
    .cards-list {
        &__items {
            margin-top: 0;
        }
    }
    &__newsline{
        .cards-list{
            &__items{
                grid-template-columns: repeat(2,1fr);
            }
        }
    }
    .news-card{
        .news-card__images{
            grid-template-columns: repeat(4,1fr);
        }
    }

    &--2{
        .stock-developer{
            &__filter{
                margin-top: 32px;
            }
        }
    }
}
