.object {

    &__body {
        background-color: var(--stand-bg);
    }

    @media (max-width:$desktop) {
        padding-top: 0;
    }
}

.object-body {
    &__slider {
        padding: 10px;
        background-color: var(--white);
        box-shadow: var(--shadow-primary);
        border-radius: 12px;

        @media (max-width:$desktop) {
            padding: 0;
            background-color: transparent;
        }
    }

    &__container {
        margin-top: 8px;
        padding-top: 16px;
        padding-bottom: 16px;

        @media (max-width:$desktop) {
            margin-top: 0;
            padding: 62px 0 16px;
        }
    }

    &__wrapper {
        margin-top: 16px;

        display: grid;
        justify-content: space-between;
        grid-template-columns: 1fr 350px;
        grid-column-gap: 16px;

        @media (max-width:$desktop) {
            margin-top: 8px;
            grid-template-columns: 1fr;
        }
    }

    &__sidebar {
        @media (max-width:$desktop) {
            margin-top: 8px;
            order: 1;
        }
    }

    &__content {
        min-width: 0;

        @media (max-width:$desktop) {
            order: 2;
        }
    }

    &__comment {
        margin-top: 16px;

        @media (max-width:$desktop) {
            margin-top: 8px;
        }
    }

    &__online-display {
        margin-top: 32px;
    }

    &__attention {}

    &__user {}

    &__data {}

    &__banners {
        margin-top: 16px;
        min-width: 100%;
        width: 100%;

        @media (max-width:$desktop) {
            margin-top: 8px;
        }
    }

    &__history {
        margin-top: 16px;

        @media (max-width:$desktop) {
            margin-top: 8px;
        }
    }

    &__location {
        margin-top: 16px;
        min-width: 100%;

        @media (max-width:$desktop) {
            margin-top: 8px;
        }
    }

    &__characteristics {
        margin-top: 16px;

        @media (max-width:$desktop) {
            margin-top: 8px;
        }
    }

    &__calc-mort {
        margin-top: 16px;

        @media (max-width:$desktop) {
            margin-top: 8px;
        }
    }

    &__similar-objects {
        margin-top: 32px;

       @media (max-width:$desktop){
        margin-top: 8px; 
       }
    }

    &__similar-city {
        margin-top: 32px;

        .search-select-one {
            max-width: 400px;
            margin-bottom: 24px;
            @media (max-width:$desktop){
                 margin: 0 16px 16px; 
            }
        }

     @media (max-width:$desktop){
        margin-top: 24px; 
     }
    }

    &__stock {
        margin-top: 25px;
        background-color: var(--white);
        box-shadow: var(--shadow-primary);
        border-radius: 12px;
        padding: 32px 24px 24px;

        @media (max-width:$desktop) {
            padding: 16px;
            margin-top: 8px;
        }
    }

    &__ask {
        background-color: var(--white);
        box-shadow: var(--shadow-primary);
        border-radius: 12px;
        padding: 32px 24px 24px;

        @media (max-width:$desktop) {
            padding: 16px;
        }
    }

    &__filter {
        margin-top: 24px;

        @media (max-width:$desktop) {
            margin-top: 8px;
        }
    }

    &__cols {
        margin-top: 24px;
        display: grid;
        grid-template-columns: 1fr 350px;
        grid-column-gap: 16px;

        @media (max-width:$desktop) {
            margin-top: 16px;
            grid-template-columns: 100%;
        }
    }

    &__col {
        min-width: 0;
        display: grid;
        grid-row-gap: 24px;
        grid-template-columns: 100%;

        @media (max-width:$desktop) {
            grid-row-gap: 16px;
        }
    }

    &__descr {
        .object-data__text {
            margin-top: 24px;
        }
    }

    &__other-develop {
        margin-top: 32px;

        @media (max-width:$desktop) {
            margin-top: 8px;
        }
    }

    &__similar-build {
        margin-top: 32px;

        @media (max-width:$desktop) {
            margin-top: 16px;
        }
    }

    &__tour {}

    &__other {
        margin-top: 32px;

        .tabs-primary__container {
            background-color: transparent;
            padding: 0;
        }

        .object-similar {
            &__top {
                justify-content: end;
            }
        }
    }
    &__banner-info{
        margin-top: 32px;
    }
}

.object-banner-info {

    &__wrapper {
        background-color: var(--primary100);
        padding: 32px;
        border-radius: 16px;
    }

    &__title {
    }

    &__descr {
        margin-top: 8px;
    }

    &__btn {
        margin-top: 24px;
        width: 425px;
    }
}



.object-other-apartment {
    box-shadow: var(--shadow-primary);
    background-color: var(--white);
    border-radius: 12px;
    padding: 32px 24px;

    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 24px;
    }

    &__title {}

    &__nav {
        display: flex;
        align-items: center;

        .nav-arrow-secondary {
            &:not(:last-child) {
                margin-right: 10px;
            }
        }

        @media (max-width:$smallTablet-max) {
            display: none;
        }
    }

    &__items {

        .swiper-wrapper {
            align-items: stretch;
        }

        .swiper-slide {
            height: auto;
        }

        .card-primary {
            height: 100%;
        }

        @media (max-width:$desktop) {
            margin: -16px;
            padding: 16px;
        }
    }

    @media (max-width:$desktop) {
        padding: 16px;
    }
}

.object-advantages {
    box-shadow: var(--shadow-primary);
    background-color: var(--white);
    padding: 32px 24px 24px;
    border-radius: 12px;

    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 24px;

        @media (max-width:$desktop) {
            margin-bottom: 16px;
        }
    }

    &__nav {
        display: flex;
        align-items: center;

        .nav-arrow-secondary--next {
            margin-left: 10px;
        }

        @media (max-width:$smallTablet-max) {
            display: none;
        }
    }

    &__title {}

    &__slider {
        @media (max-width:$desktop) {
            padding: 0 16px;
            margin: 0 -16px;
        }
    }

    &__items {}

    &__btn {
        margin-top: 24px;
        width: 100%;

        &._active {
            position: relative;
            font-size: 0;

            &::before {
                content: 'Свернуть';
                font-size: var(--default-fz);
            }
        }
    }

    .advantages-card {
        width: 100%;

        &__container {
            width: 100%;
        }
    }

    &._active {
        .object-advantages {
            &__slider {
                overflow: visible;
            }

            &__nav {
                display: none;
            }

            &__items {
                display: grid;
                grid-template-columns: repeat(2, 1fr);
                grid-column-gap: 16px;
                grid-row-gap: 32px;

                @media (max-width:$mobile-max) {
                    grid-template-columns: 1fr;
                }
            }
        }
    }

    @media (max-width:$desktop) {
        padding: 16px 16px 24px;
    }
}

.object-filter {
    box-shadow: var(--shadow-primary);
    background-color: var(--white);
    padding: 32px 24px;
    border-radius: 16px;

    &__top {
        margin-bottom: 32px;
        display: flex;
        align-items: start;
        justify-content: start;
        flex-direction: column;
        @media (max-width:$desktop) {
            margin-bottom: 16px;
            padding: 0 16px;
        }
    }

    &__date {
        margin-top: 8px;
        color: var(--primaryTwo);
        font-size: var(--small-fz);
        line-height: var(--small-lh);
        font-weight: 400;
    }

    .filter__btn {
        @media (max-width:$desktop) {
            margin: 0 16px;
            width: calc(100% - 32px);
        }
    }

    &__row {
        &--1 {
            grid-template-columns: percent(419, 1132) percent(278, 1132) percent(419, 1132);
        }

        &--2 {
            grid-template-columns: percent(375, 1132) percent(435, 1132) 1fr;
            justify-content: start;

            .filter__item--checkboxes {
                margin-left: 8px;
                display: flex;
                align-items: center;
                justify-self: end;

                >.checkbox-secondary {
                    &:not(:last-child) {
                        margin-right: 20px;

                        @media (max-width:$desktop) {
                            margin-right: 0;
                            margin-bottom: 14px;
                        }
                    }
                }

                @media (max-width:$desktop) {
                    margin: 0;
                    align-self: start;
                    flex-direction: column;
                }
            }

            .filter__item {
                &:nth-child(2) {
                    .choices__list--single {
                        padding: 0 30px 0 115px;
                    }
                }
            }
        }
    }

    &__sort {
        .choices__list--dropdown {
            width: max-content;
        }

        @media (max-width:$desktop) {
            padding: 0 $containerOffset+px;
            margin-top: 16px;
            justify-content: start;
        }
    }

    &__tabs {
        margin-top: 40px;

        .tabs-primary__inner {
            margin-top: 0;

            @media (max-width:$desktop) {
                padding-top: 0;
            }
        }

        .tabs-primary__container {
            padding: 0;
            margin-top: 32px;
            background-color: var(--white);

            @media (max-width:$desktop) {
                margin-top: 16px;
            }
        }

        .tabs-primary__content {
            margin-top: 0;
        }

        @media (max-width:$desktop) {
            margin-top: 8px;
        }
    }

    &__checkboard {}

    &__layouts {}

    @media (max-width:$desktop) {
        padding: 16px 0 0;
    }
}

.object-characteristics {
    background-color: var(--white);
    box-shadow: var(--shadow-primary);
    border-radius: 12px;
    padding: 32px 24px 24px;

    &__title {
        margin-bottom: 32px;

        @media (max-width:$desktop) {
            margin-bottom: 16px;
        }
    }

    &__list {
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 110px;
        grid-row-gap: 16px;

        &:not(:first-child) {
            margin-top: 16px;
        }

        &:not([hidden]) {
            display: grid;
        }

        @media (max-width:$smallTablet-max) {
            grid-template-columns: 1fr;
            grid-row-gap: 8px;

            &:first-child {
                margin-top: 16px;
            }

            &:not(:first-child) {
                margin-top: 8px;
            }
        }
    }

    &__item {
        display: flex;
        align-items: center;
        width: 100%;

        &::after {
            content: '';
            flex: 1 1 0%;
            align-self: end;
            position: relative;
            top: -3px;
            border-bottom: 1px dashed var(--primary200);
            height: 1px;
            order: 2;
            margin: 0 6px;
        }

        span {
            font-size: var(--default-fz);
            line-height: var(--default-lh);
            font-weight: 400;

            &:nth-child(1) {
                order: 1;
                color: var(--primaryTwo);

                @media (max-width:$smallTablet-max) {
                    width: 50%;
                }
            }

            &:nth-child(2) {
                order: 3;

                @media (max-width:$smallTablet-max) {
                    width: 42%;
                }
            }
        }

        @media (max-width:$smallTablet-max) {
            justify-content: space-between;
        }
    }

    &__more {
        display: flex;
        align-items: center;
        margin-top: 16px;
        color: var(--blue);
        font-size: var(--small-fz);
        line-height: 1;

        svg {
            margin-left: 5px;
            width: 20px;
            height: 20px;
            fill: currentColor;
            transition: transform $transitionSpeedFast $transitionFunction;
        }

        &._active {
            svg {
                transform: rotate(-180deg);
            }
        }

        @media (max-width:$desktop) {
            margin-top: 12px;
        }
    }

    @media (max-width:$desktop) {
        padding: 16px;
    }
}

// ================================================================================
.object-comment {
    border-radius: 12px;
    background-color: var(--white);
    box-shadow: var(--shadow-primary);
    padding: 32px 24px;

    &__title {}

    &__descr {
        margin-top: 24px;
        font-size: var(--default-fz);
        line-height: var(--default-lh);
        color: var(--dark);

        @media (max-width:$desktop) {
            margin-top: 16px;
        }
    }

    @media (max-width:$desktop) {
        padding: 16px 16px 24px;
    }
}

.object-attention {
    border-radius: 12px;
    background-color: var(--white);
    box-shadow: var(--shadow-primary);
    padding: 32px 24px;

    &__title {}

    &__content {
        margin-top: 1px;
        font-size: var(--default-fz);
        line-height: var(--default-lh);
        color: var(--dark);
        font-weight: 400;

        a {
            color: var(--blue);
            font-size: var(--little-big-fz);
            ;
            line-height: var(--little-big-lh);
            font-weight: 500;
            transition: color $transitionSpeed $transitionFunction;

