            margin-top: 24px;
            justify-content: center;
        }
    }

    &__add,
    &__no {
        padding: 0 48px;
    }

    &__add {}

    &__no {
        margin-left: 32px;

        @media (max-width:$desktop) {
            margin-left: 16px;
        }
    }

    @media (max-width:$desktop) {
        padding: 16px;
    }
}

.object-data {
    background-color: var(--white);
    box-shadow: var(--shadow-primary);
    padding: 32px 24px 24px;
    border-radius: 12px;
    &__statistics{

    }
    &__statistics-item{
        display: flex;
        align-items: center;
        width: 280px;

        font-size: var(--small-fz);
        line-height: var(--smal-lh);
        svg{
            width: 14px;
            height: 14px;
            fill: var(--dark);
        }
        >div{
            width: 65px;
            display: flex;
            align-items: center;
            color: var(--dark);
            span{
                margin-left: 8px;
            }
            &:not(:last-child){
                margin-right: 16px;
            }
        }
        >span{
            &:first-child{
                margin-right: 24px;
                width: 140px;

                color: var(--primaryTwo);
            }
        }

        &:not(:last-child){
            margin-bottom: 8px;
        }
    }
    &__time {
        color: var(--primary600);
        font-size: var(--small-fz);
        line-height: 1;
        font-weight: 500;
        padding: 7px 9.5px;
        background-color: var(--primary100);
        border-radius: 8px;
    }

    &__price {
        margin-top: 24px;
        display: flex;
        flex-direction: column;
    }

    &__price-new {
        span {
            &:nth-child(1) {
                font-size: var(--big-small-fz);
                line-height: var(--big-small-lh);
            }

            &:nth-child(2) {
                color: var(--primaryTwo);
                font-size: var(--default-fz);
                line-height: var(--default-lh);
                font-weight: 400;
                margin-left: 5px;
            }
        }
    }

    &__price-old {
        margin-bottom: 5px;
        display: flex;
        align-items: center;
        color: var(--primaryTwo);

        span {
            &:nth-child(1) {
                color: var(--primaryTwo);
                font-size: var(--default-fz);
                line-height: var(--default-lh);
                font-weight: 400;

                span {
                    position: relative;

                    &::before {
                        content: '';
                        border-bottom: 1px solid currentColor;
                        position: absolute;
                        width: 100%;
                        height: 50%;
                        transform: rotate(-5deg);
                    }
                }
            }

            &:nth-child(2) {
                margin-left: 5px;
                padding: 3px 10px;
                color: var(--green);
                background: rgba(36, 202, 142, 0.1);
                border-radius: 8px;
                font-size: var(--small-fz);
                line-height: var(--small-lh);
                font-weight: 400;
            }
        }
    }


    &__name {
        font-size: var(--big-fz);
        line-height: var(--big-lh);
        font-weight: 500;
        color: var(--dark);
    }

    &__address {
        margin-top: 5px;
        font-size: var(--default-fz);
        line-height: var(--default-lh);

        &--one {
            display: block;
            font-weight: 500;
            color: var(--dark);
        }

        &--two {
            &:not(:first-child) {
                margin-top: 5px;
            }

            display: block;
            color: var(--primary600)
        }

        @media (max-width:$desktop) {
            margin-top: 4px;
        }
    }

    &__mortgage {
        margin-top: 16px;
        color: var(--primaryTwo);
        font-size: var(--default-fz);
        line-height: var(--default-lh);
        font-weight: 400;

        span {
            color: var(--blue);
            font-weight: 500;
        }

        @media (max-width:$smallTablet-max) {
            margin-left: 0;

            span {
                margin-top: 4px;
            }
        }
    }

    &__text {
        margin-top: 32px;
        display: -webkit-box;
        -webkit-line-clamp: 4;
        -webkit-box-orient: vertical;
        overflow: hidden;

        p {
            color: var(--dark);
            font-size: var(--default-fz);
            line-height: var(--default-lh);
        }

        &._active {
            display: block;
        }

        @media (max-width:$desktop) {
            margin-top: 16px;
            -webkit-line-clamp: 3;
        }

        @media (max-width:$mobile-max) {
            -webkit-line-clamp: 6;
        }
    }

    &__more {
        display: flex;
        align-items: center;
        margin-top: 16px;
        color: var(--blue);

        span {
            font-size: var(--small-fz);
            line-height: 1;
        }

        svg {
            margin-left: 5px;
            width: 20px;
            height: 20px;
            fill: currentColor;
        }

        @media (max-width:$desktop) {
            margin-top: 8px;

            svg {
                margin-left: 4px;
            }
        }
    }

    &__characteristics {
        margin-top: 32px;

        .object-characteristics {

            &__container {}

            &__list {
                @media (min-width:$smallTablet-min) {
                    grid-template-columns: 1.5fr 1fr;
                    grid-column-gap: 73px;
                }
            }

            &__item {
                span {
                    width: auto;

                    &:nth-child(2) {
                        width: 30%;
                    }
                }
            }

            &__more {}
        }

        padding: 0;
        box-shadow: none;
        background-color: transparent;
        border-radius: 0;

        @media (max-width:$smallTablet-max) {
            margin-top: 24px;
        }
    }

    &__tooltip {
        margin-left: auto;
        display: flex;
        align-items: center;

        .secondary-tooltip {
            width: auto;
            height: auto;

            &__btn {
                svg {
                    width: 20px;
                    height: 20px;
                    stroke: var(--primaryTwo);
                    stroke-width: 1.5px;
                }
            }

            &__content {
                width: max-content;
                max-width: 200px;
                bottom: 24px;
                left: 0;
                transform: translate(0);
                z-index: 10;
            }
        }

        >span {
            font-size: var(--default-fz);
            line-height: var(--default-lh);
            color: var(--blue);
            font-weight: 500;
            margin-left: 5px;
        }

        @media (max-width:$smallTablet-max) {
            margin-top: 16px;
            margin-left: 0;
        }
    }

    &__purchase-terms {
        margin-top: 24px;
        border-radius: 12px;
        padding: 32px 24px 24px;
        background-color: var(--white);
        -webkit-box-shadow: var(--shadow-primary);
        box-shadow: var(--shadow-primary);

        @media (max-width:$smallTablet-max) {
            margin-top: 24px;
        }
    }

    &__online-display {
        margin-top: 48px;

        @media (max-width:$smallTablet-max) {
            margin-top: 32px;
        }
    }

    .row {
        display: flex;
        align-items: center;

        &:not(:first-child) {
            flex-direction: column;
            align-items: start;
        }

        @media (max-width:$smallTablet-max) {
            flex-direction: column;
            align-items: start;
        }
    }

    &__rating {
        margin-left: 32px;
        display: flex;
        align-items: center;

        >div {
            display: flex;
            align-items: center;

            svg {
                fill: var(--yellow);
                width: 16px;
                height: 16px;

                &:not(:last-child) {
                    margin-right: 4px;
                }
            }
        }

        >span {
            color: var(--primary600);
            font-size: var(--small-fz);
            line-height: var(--small-lh);
            margin-left: 4px;

            &:nth-child(2) {
                font-weight: 500;
            }

            &:nth-child(3) {
                font-weight: 400;
            }
        }

        @media (max-width:$smallTablet-max) {
            margin: 4px 0 0 0;
        }
    }

    &__choices {
        margin-top: 24px;
    }

    @media (max-width:$desktop) {
        padding: 16px;
    }
}




.object-banners {
    padding: 24px;
    background-color: var(--white);
    box-shadow: var(--shadow-primary);
    border-radius: 12px;

    .main-banner {
        padding: 32px;
    }

    @media (max-width:$desktop) {
        padding: 16px 0;
    }
}



.object-history {
    padding: 32px 24px;
    background-color: var(--white);
    box-shadow: var(--shadow-primary);
    border-radius: 12px;

    &__title {}

    &__list {
        margin-top: 32px;
        display: flex;
        flex-direction: column;

        @media (max-width:$desktop) {
            margin-top: 16px;
        }

        @media (max-width:$smallTablet-max) {
            display: flex;
        }
    }

    &__item {
        display: grid;
        align-items: center;
        grid-template-columns: max-content 1.5fr 1fr;
        grid-column-gap: 100px;
        padding: 20px 0;

        &:first-child {
            padding-top: 0;
        }

        &:not(:last-child) {
            border-bottom: 1px solid var(--primary100);
