.object-location {
    background-color: var(--white);
    box-shadow: var(--shadow-primary);
    border-radius: 12px;
    padding: 32px 24px;

    &__title {
        @media (max-width:$desktop) {
            padding: 0 16px;
        }
    }

    &__descr {
        margin-top: 16px;
        color: var(--primary600);
        font-size: var(--default-fz);
        line-height: var(--default-lh);

        @media (max-width:$desktop) {
            padding: 0 16px;
        }
    }

    &__btns {
        margin-top: 24px;
        display: inline-flex;
        align-items: center;
        border-bottom: 1px solid var(--primary200);

        @media (max-width:$desktop) {
            overflow-x: scroll;
            display: flex;
            -ms-overflow-style: none;
            @include hiddenScroll;
        }
    }

    &__btn {
        color: var(--primaryTwo);
        font-size: var(--default-fz);
        line-height: var(--default-lh);
        transition: background-color $transitionSpeed $transitionFunction,
            color $transitionSpeed $transitionFunction;
        padding: 9px 20px;
        display: flex;
        align-items: center;
        position: relative;

        &:not(:last-child) {
            margin-right: 24px;
        }

        &::before {
            content: '';
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            width: 0;
            height: 2px;
            background-color: var(--blue);
            transition: width $transitionSpeed $transitionFunction;
        }

        @media (min-width:$desktop-min) {

            &:hover,
            &.focus-visible {
                color: var(--blue);
            }
        }

        @media (max-width:$desktop) {
            flex-grow: 1;
            justify-content: center;
            padding: 5px 16px 4px;
            white-space: nowrap;
        }

        &._active {
            color: var(--blue);

            &::before {
                width: 100%;
            }
        }
    }

    &__infrastructure {
        margin-top: 16px;
        display: none;
        align-items: center;
        overflow-x: auto;
        @include hiddenScroll;

        @media (max-width:$desktop) {
            padding: 0 16px;
        }

        &._active {
            display: flex;
        }

        &._active-fullscreen {
            display: flex;
            flex-wrap: wrap;
            position: absolute;
            bottom: 16px;
            left: 16px;
            right: 16px;

            .object-location__infrastructure-btn {
                background-color: var(--blue);
                color: var(--white);

                span {
                    color: var(--white);
                }
            }
        }

        .tag {
            &:not(:last-child) {
                margin-right: 8px;
            }

            span {
                margin-left: 8px;
                color: var(--primaryTwo);
                transition: color $transitionSpeed $transitionFunction;
            }

            &._active {
                span {
                    color: var(--white);
                }
            }

            @media (min-width:$desktop-min) {
                &:hover {
                    span {
                        color: var(--white);
                    }
                }
            }
        }
    }

    &__infrastructure-btn {
        height: 34px;
        border-radius: 10px;
        font-size: var(--small-fz);
        line-height: 1;
        padding: 0 24px;

        color: var(--dark);
        background-color: rgba(0, 150, 255, .08);
        border: none;
        outline: none;
        white-space: nowrap;

        display: flex;
        align-items: center;

        span {
            margin-left: 8px;
            color: var(--primaryTwo);
            transition: color $transitionSpeed $transitionFunction;
        }

        &:not(:last-child) {
            margin-right: 8px;
        }

        &._active {
            background-color: rgba(0, 150, 255, .12);
            color: var(--dark);
        }

        @media (min-width:$desktop-min) {

            &:hover,
            &.focus-visible {
                background-color: rgba(0, 150, 255, .12);
                color: var(--dark);
            }
        }
    }

    &__maps {
        margin-top: 16px;
        width: 100%;
        background-color: var(--white);
        border-radius: 12px;
        position: relative;

        @media (max-width:$desktop) {
            margin-top: 16px;
        }

        .map-primary {
            width: 100%;
            height: 423px;
            border-radius: 12px;

            @media (max-width:$desktop) {
                height: 350px;
            }

            @media (max-width:$smallTablet-max) {
                height: 227px;
            }
        }
    }

    &__routes {
        display: none;
        position: absolute;
        z-index: 2;
        top: 16px;
        right: 78px;

        &._active {
            display: block;
        }

        &._show {
            z-index: -1;
        }

        &._active-fullscreen {}
    }

    @media (max-width:$desktop) {
        padding: 16px 0;
    }
}



